import React, { useCallback, useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { message, Button } from 'antd';
import { answerQuestion, answerLike, answerPin, answerUnLike, answerUnPin, answerInsert } from '../services';
import { AnswerModal } from '../containers';
import { Answer } from '../components';

export default function Answers () {
  const [visible, setVisible] = useState(false);
  const navigate = useNavigate();
  const [params] = useSearchParams();
  const questionId = params.get('id')
  const [answers, setAnswers] = useState([]);

  const handleAnswerPin = async (answerId, pin) => {
    const service = pin ? answerUnPin : answerPin;
    const { message: text } = await service({ answerId });
    if (text) {
      message.success(`${pin ? 'UnPin' : 'Pin'} answer ${answerId} ${text}`)
      getQuestionAnswers({ questionId })
    }
  }
  const handleAnswerLike = async (answerId, like) => {
    const service = like ? answerUnLike : answerLike;
    const { message: text } = await service({ answerId });
    if (text) {
      message.success(`${like ? 'UnLike' : 'Like'} answer ${answerId} ${text}`)
      getQuestionAnswers({ questionId })
    }
  }
  const handleAnswerComment = (id) => {
    navigate(`/answer?id=${id}&questionId=${questionId}`)
  }

  const getQuestionAnswers = useCallback(async (params) => {
    const { data = [] } = await answerQuestion(params);
    setAnswers(data)
  }, [])

  useEffect(() => {
    if (questionId) {
      getQuestionAnswers({ questionId });
    }
  }, [getQuestionAnswers, questionId]);

  return (
    <>
     {answers.length > 0 && (<Button type='primary' onClick={() => setVisible(!visible)} size='small' style={{ float: 'right', zIndex: 99 }}>Add An Answer</Button>)}
      <AnswerModal
        visible={visible}
        setVisible={setVisible}
        service={answerInsert}
        questionId={questionId}
        callback={() => getQuestionAnswers({ questionId })}
      />
      <Answer
        dataSource={answers}
        onPin={handleAnswerPin}
        onLike={handleAnswerLike}
        onComment={handleAnswerComment}
        onClick={() => setVisible(!visible)}
      />
    </>

  );
}
