import React from 'react';
import { Col, Row, Button, Input, Tooltip } from 'antd';
import { PlusCircleOutlined } from '@ant-design/icons';

const { Search } = Input;

export default function CustomSearch ({ value, onChange, onSearch, disabled, onClick }) {
  return (
        <Col span={24}>
            <Row align='middle' justify='space-between'>
                <Col>
                    <Search
                        placeholder="input search text"
                        value={value}
                        onChange={onChange}
                        style={{ width: '100%' }}
                        onSearch={onSearch}
                    /></Col>
                <Col>
                    <Tooltip title='Add New Question'>
                        <Button
                            type='primary'
                            size='small'
                            disabled={disabled}
                            onClick={onClick}
                            icon={<PlusCircleOutlined />
                            }
                        />
                    </Tooltip>
                </Col>
            </Row>
        </Col>
  )
}
