import React, { useState } from 'react';
import { Input, Progress } from 'antd';

const pwReg = {
  weak: /[0-9]/,
  normal: /[a-zA-Z]/,
  strong: /[^0-9a-zA-Z_]/,
}

const checkPwStrength = (value) => {
  let level = 0;
  if (pwReg.weak.test(value)) {
    level++;
  }
  if (pwReg.normal.test(value)) {
    level++;
  }
  if (pwReg.strong.test(value)) {
    level++;
  }
  return level;
}

const strength2progress = [
  { percent: 0, strokeColor: '#f2f2f2' },
  { percent: 33, strokeColor: 'red' },
  { percent: 66, strokeColor: 'yellow' },
  { percent: 100, strokeColor: 'green' },
]

export default function PasswordStrength (props) {
  const [progress, setProgress] = useState({
    percent: 0,
    strokeColor: '#fff'
  })
  const { value, onChange } = props;

  const hanldeInputChange = (e) => {
    const value = e.target.value;
    setProgress({
      ...progress,
      ...strength2progress[checkPwStrength(value)]
    })
    if (onChange) {
      onChange(value)
    }
  }
  return (
        <>
            <Input.Password onChange={hanldeInputChange} value={value} />
            <Progress {...progress} size="small" showInfo={false} />
        </>
  )
}
