import React, { useCallback, useEffect, useState } from 'react';
import { Layout, Col, Row, Divider } from 'antd';
import { useNavigate, Outlet } from 'react-router-dom';
import Header from './header';
import { Course, Category, Question, Search } from '../components';
import { QuestionModal } from '../containers';
import { currentUser, courses, categorys, questionCourse, questionCourseCategory, questionSearch, questionInsert } from '../services';

const { Content, Footer, Sider } = Layout;

export default function CustomLayout () {
  const navigate = useNavigate();
  const [value, setValue] = useState('');
  const [keyword, setKeyword] = useState('');
  const [visible, setVisible] = useState(false);
  const [questionVisible, setQuestionVisible] = useState(false);
  const [userInfo, setUserInfo] = useState({ email: '', username: '', id: '', avatar: '' })
  const [course, setCourse] = useState([]);
  const [category, setCategory] = useState([]);
  const [questions, setQuestions] = useState([]);

  const [courseId, setCourseId] = useState(0);
  const [categoryId, setCategoryId] = useState(0);
  const [questionId, setQuestionId] = useState(0);

  const getCurrentUser = useCallback(async () => {
    const res = await currentUser();
    if (res.id) {
      setUserInfo({ ...res })
    } else {
      navigate('/login')
    }
  }, [])

  const getCourses = useCallback(async () => {
    const { data = [] } = await courses();
    setCourse(data)
  }, [])

  const getCategorys = useCallback(async () => {
    const { data = [] } = await categorys();
    setCategory(data)
  }, [])

  const getSearchQuestions = useCallback(async (params) => {
    const { data = [] } = await questionSearch(params);
    setQuestions(data)
  }, [])

  const getCourseQuestions = useCallback(async (params) => {
    const { data = [] } = await questionCourse(params);
    setQuestions(data)
  }, [])

  const getCourseCategoryQuestions = useCallback(async (params) => {
    const { data = [] } = await questionCourseCategory(params);
    setQuestions(data)
  }, [])

  const onQuestionClick = (id) => {
    navigate(`/answers?id=${id}`)
  }

  const handleValueChange = (e) => {
    setValue(e.target.value);
    setCategoryId(0)
    setCourseId(0)
  }

  useEffect(() => {
    getCurrentUser();
  }, [getCurrentUser]);

  useEffect(() => {
    getCourses();
  }, [getCourses]);

  useEffect(() => {
    getCategorys();
  }, [getCategorys]);

  useEffect(() => {
    if (keyword) {
      getSearchQuestions({ keyword })
    } else if (courseId) {
      if (categoryId) {
        getCourseCategoryQuestions({ courseId, categoryId });
      } else {
        getCourseQuestions({ courseId });
      }
    }
    navigate('/dashboard')
  }, [getCourseQuestions, courseId, categoryId, keyword]);

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <QuestionModal
        visible={questionVisible}
        setVisible={setQuestionVisible}
        service={questionInsert}
        courseId={courseId}
        categoryId={categoryId}
        callback={() => getCourseCategoryQuestions({ courseId, categoryId })}
      />
      <Header
        visible={visible}
        setVisible={setVisible}
        getCourses={getCourses}
        getCurrentUser={getCurrentUser}
        userInfo={userInfo}
      />
      <Layout>
        <Sider
          width={600}
          breakpoint="lg"
          collapsedWidth="0"
          style={{ backgroundColor: '#fff', padding: 8 }}
        >
          <Row>
            <Col span={12}>
              <Row gutter={24}>
                <Course
                  onClick={() => setVisible(!visible)}
                  dataSource={course}
                  courseId={courseId}
                  setCourseId={setCourseId}
                  callback={() => setKeyword('')}
                />
                <Category
                  dataSource={category}
                  categoryId={categoryId}
                  setCategoryId={setCategoryId}
                  callback={() => setKeyword('')}
                />
              </Row>
            </Col>
            <Col span={1}><Divider type="vertical" style={{ minHeight: '100vh' }} /></Col>
            <Col span={11}>
              <Row>
                <Search
                  value={value}
                  onChange={handleValueChange}
                  onSearch={(value) => setKeyword(value)}
                  disabled={!(courseId > 0 && categoryId > 0)}
                  onClick={() => setQuestionVisible(!questionVisible)}
                />
                <Question
                  dataSource={questions}
                  questionId={questionId}
                  setQuestionId={setQuestionId}
                  onClick={onQuestionClick}
                />
              </Row>
            </Col>
          </Row>
        </Sider>
        <Content style={{ margin: '24px 16px 0', background: '#fff' }}>
          <div style={{ padding: 24, minHeight: 360 }}>
            <Outlet />
          </div>
        </Content>
      </Layout>
      <Footer style={{ textAlign: 'center' }}>Online Discussion Forum</Footer>
    </Layout>
  );
}
