import { request } from '../utils';

export async function register (data) {
  return request('/api/users/register', {
    method: 'POST',
    data,
  });
}

export async function login (data) {
  return request('/api/users/login', {
    method: 'POST',
    data,
  });
}

export async function currentUser () {
  return request('/api/users/current', {
    method: 'GET',
  });
}

export async function currentUserUpdate (data) {
  return request('/api/users/current', {
    method: 'PUT',
    data
  });
}

export async function resetUserPw (data) {
  return request('/api/users/resetPassword', {
    method: 'POST',
    data
  });
}

export async function resetUserCode (data) {
  return request('/api/users/resetCode', {
    method: 'POST',
    data
  });
}

export async function logout () {
  localStorage.clear();
  return true
}
