import { request } from '../utils';

export async function answerInsert (data) {
  return request('/api/answer', {
    method: 'POST',
    data
  });
}

export async function answerLike ({ answerId }) {
  return request(`/api/answer/like/${answerId}`, {
    method: 'GET',
  });
}

export async function answerUnLike ({ answerId }) {
  return request(`/api/answer/unLike/${answerId}`, {
    method: 'GET',
  });
}

export async function answerQuestion ({ questionId }) {
  return request(`/api/answer/question/${questionId}`, {
    method: 'GET',
  });
}

export async function answerPin ({ answerId }) {
  return request(`/api/answer/pin/${answerId}`, {
    method: 'GET'
  });
}

export async function answerUnPin ({ answerId }) {
  return request(`/api/answer/unPin/${answerId}`, {
    method: 'GET'
  });
}
