import React from 'react';
import { Col, Row, Space, Typography, Button, Tooltip } from 'antd';
import { FolderAddOutlined } from '@ant-design/icons';

const { Title, Text } = Typography

export default function Course ({ onClick, dataSource = [], courseId, setCourseId, callback }) {
  const handleClick = (id) => {
    setCourseId(id)
    callback()
  }
  return (
    <Col span={24}>
      <Space direction="vertical" style={{ width: '100%', }}>
        <Row justify='space-between' align='middle'>
          <Col span={6}><Title level={4}>Course</Title></Col>
          <Col span={2}>
            <Tooltip title='Add New Course'>
              <Button
                type='primary'
                size='small'
                icon={<FolderAddOutlined />}
                onClick={onClick}
              />
            </Tooltip>
          </Col>
        </Row>
        {
          dataSource.map(item =>
            <Text
              key={item.id}
              style={{
                width: '100%',
                padding: 2,
                display: 'inline-block',
                background: courseId === item.id ? '#96b2cb' : '#fff',
                color: courseId === item.id ? '#fff' : 'rgba(0, 0, 0, 0.85)',
                cursor: 'pointer',
              }}
              onClick={() => handleClick(item.id)}
            >
              {item.courseName}
            </Text>
          )
        }
      </Space>
    </Col>
  )
}
