import React, { useState, useCallback, useEffect, createElement } from 'react';
import { Comment, Tooltip, Space, Input, Form, Button, message } from 'antd';
import { RollbackOutlined, PushpinOutlined, LikeOutlined } from '@ant-design/icons';
import { useSearchParams, useNavigate } from 'react-router-dom';
import { Comments } from '../components';
import { answerQuestion, answerLike, answerPin, answerUnLike, answerUnPin, review } from '../services';

const IconText = ({ icon, text, ...props }) => (
    <Space>
        {createElement(icon, props)}
        {text}
    </Space>
);

const Answer = () => {
  const [form] = Form.useForm();
  const navigator = useNavigate();
  const [params] = useSearchParams();
  const [answer, setAnswer] = useState({});
  const answerId = params.get('id')
  const questionId = params.get('questionId')

  const getQuestionAnswers = useCallback(async (params) => {
    const { data = [] } = await answerQuestion(params);
    setAnswer(data.find(item => item.id === Number(answerId)))
  }, [])

  useEffect(() => {
    if (questionId) {
      getQuestionAnswers({ questionId });
    }
  }, [getQuestionAnswers, questionId]);

  const onPin = async (answerId, pin) => {
    const service = pin ? answerUnPin : answerPin;
    const { message: text } = await service({ answerId });
    if (text) {
      message.success(`${pin ? 'UnPin' : 'Pin'} answer ${answerId} ${text}`)
      getQuestionAnswers({ questionId })
    }
  }
  const onLike = async (answerId, like) => {
    const service = like ? answerUnLike : answerLike;
    const { message: text } = await service({ answerId });
    if (text) {
      message.success(`${like ? 'UnLike' : 'Like'} answer ${answerId} ${text}`)
      getQuestionAnswers({ questionId })
    }
  }

  const handleAddComment = async () => {
    form.validateFields().then(async value => {
      const { message: text } = await review({
        answerId,
        ...value
      })
      if (text) {
        message.success(`Add Comment ${text}!`)
        getQuestionAnswers({ questionId })
        form.resetFields()
      }
    })
  }

  const actions = [
        <IconText icon={PushpinOutlined} style={{ cursor: 'pointer', color: answer.pin ? '#51288c' : 'rgba(0, 0, 0, 0.45)', }} key="pin" onClick={() => onPin(answer.id, answer.pin)} />,
        <IconText icon={LikeOutlined} style={{ cursor: 'pointer', color: answer.like ? '#51288c' : 'rgba(0, 0, 0, 0.45)', marginLeft: 8 }} key="like" onClick={() => onLike(answer.id, answer.like)} />,
  ];

  return (

        <>
        <RollbackOutlined style={{ cursor: 'pointer', color: '#51288c' }} onClick={() => navigator(`/answers?id=${questionId}`)}/>
            <Comment
                actions={actions}
                author={<a>{answer.user ? answer.user.username : '-'}</a>}
                content={
                    <p>
                        {answer.content}
                    </p>
                }
                datetime={
                    <Tooltip title={answer.updatedAt ? answer.updatedAt.split('T')[0] : '-'}>
                        <span>{answer.updatedAt ? answer.updatedAt.split('T')[0] : '-'}</span>
                    </Tooltip>
                }
            />
            {answer.reviews && answer.reviews.length > 0 && <Comments dataSource={answer.reviews} />}
            <Form
                form={form}
                autoComplete="off"
            >
                <Form.Item
                    name="content"
                    rules={[{ required: true, message: 'Please input your content!' }]}
                >
                    <Input.TextArea rows={4} />
                </Form.Item>
                <Form.Item>
                    <Button onClick={handleAddComment}>
                        Add Comment
                    </Button>
                </Form.Item>
            </Form>

        </>
  );
};

export default Answer;
