import React from 'react';
import { Button, Form, Input, Typography, message, Row, Col } from 'antd';
import { useNavigate } from 'react-router-dom';
import { PasswordStrength } from '../components';
import { register } from '../services';

const { Title } = Typography;

export default function Register () {
  const navigate = useNavigate();

  const onFinish = async (values) => {
    const { username, password, confirmPassword, email } = values;
    if (password !== confirmPassword) {
      message.error('Please enter same password');
      return;
    }
    const { token } = await register({ username, email, password });
    if (token) {
      localStorage.setItem('token', token)
      navigate('/dashboard');
    }
  };
  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };

  return (
    <Row align='middle' justify='center' style={{ minHeight: '400px', margin: 24 }}>
      <Col span={24} style={{ textAlign: 'center' }}>
        <Title>Register</Title>
      </Col>
      <Col span={24}>

        <Form
          labelCol={{ span: 8 }}
          wrapperCol={{ span: 8 }}
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          autoComplete='off'>
          <Form.Item
            label='Username'
            name='username'
            rules={[
              {
                required: true,
                message: 'Please input your username!',
              },
            ]}>
            <Input />
          </Form.Item>
          <Form.Item
            label='Email'
            name='email'
            rules={[
              {
                required: true,
                message: 'Please input your email!',
              },
            ]}>
            <Input />
          </Form.Item>

          <Form.Item
            label='Password'
            name='password'
            htmlFor='password'
            rules={[
              {
                required: true,
                message: 'Please input your password!',
              },
            ]}>
            <PasswordStrength />
          </Form.Item>
          <Form.Item
            label='Confirm password'
            name='confirmPassword'
            rules={[
              {
                required: true,
                message: 'Please confirm your password!',
              },
            ]}>
            <Input.Password />
          </Form.Item>

          <Form.Item wrapperCol={{ offset: 8, span: 8 }}>
            <Button type="primary" htmlType="submit" block>
              Submit
            </Button>
            Or <a href="/login">login now!</a>
          </Form.Item>
        </Form>
      </Col>
    </Row>
  );
}
