import React, { useEffect } from 'react';
import { Button, Form, Input, Typography, Row, Col, Checkbox } from 'antd';
import { useNavigate } from 'react-router-dom';
import { login } from '../services';
import { clearCookie, getCookie, setCookie } from '../utils';

const { Title } = Typography;

export default function Login () {
  const navigate = useNavigate();
  const [form] = Form.useForm();
  const onFinish = async () => {
    form.validateFields().then(async values => {
      const { password, email, remember } = values;
      const { token } = await login({ email, password });
      if (token) {
        if (remember) {
          setCookie('email', email, 7)
          setCookie('password', password, 7)
        } else {
          clearCookie()
        }
        localStorage.setItem('token', token)
        navigate('/dashboard');
      }
    })
  };

  useEffect(() => {
    const email = getCookie('email');
    const password = getCookie('password');
    if (email && password) {
      form.setFieldsValue({
        email,
        password
      })
    }
  }, [form])

  return (
    <Row align='middle' justify='center' style={{ minHeight: '400px', margin: 24 }}>
      <Col span={24} style={{ textAlign: 'center' }}>
        <Title>Login</Title>
      </Col>
      <Col span={24}>
        <Form
         form={form}
          name="basic"
          labelCol={{ span: 8 }}
          wrapperCol={{ span: 8 }}
          initialValues={{ remember: true }}
          autoComplete="off"
        >
          <Form.Item
            label="Email"
            name="email"
            rules={[{ required: true, message: 'Please input your email!' }]}
          >
            <Input />
          </Form.Item>

          <Form.Item
            label="Password"
            name="password"
            rules={[{ required: true, message: 'Please input your password!' }]}
          >
            <Input.Password />
          </Form.Item>
          <Form.Item wrapperCol={{ offset: 8, span: 8 }}>
            <Form.Item name="remember" valuePropName="checked" noStyle>
              <Checkbox>Remember me</Checkbox>
            </Form.Item>
            <a style={{ float: 'right' }} href="/forgot">
              Forgot password
            </a>
          </Form.Item>

          <Form.Item wrapperCol={{ offset: 8, span: 8 }} onClick={onFinish}>
            <Button type="primary" block>
              Submit
            </Button>
            Or <a href="/register">register now!</a>
          </Form.Item>
        </Form>
      </Col>
    </Row>
  );
}
