import React from 'react';
import { Modal, Form, Input, message } from 'antd';

export default function QuestionModal ({ visible, setVisible, callback, service, courseId, categoryId }) {
  const [form] = Form.useForm();
  const handleOk = async () => {
    form.validateFields().then(async values => {
      const { message: text } = await service({
        ...values,
        courseId,
        categoryId
      });
      if (text) {
        message.success(`Add New Quetsion ${text}!`)
        callback();
        form.resetFields();
        setVisible(!visible)
      }
    })
  }
  return (
    <Modal
    key='Add New Quetsion'
    title='Add New Quetsion'
    open={visible}
    onCancel={() => setVisible(!visible)}
    onOk={handleOk}
  >
        <Form
          form={form}
          autoComplete="off"
        >
          <Form.Item
            label="quetsion"
            name="content"
            rules={[{ required: true, message: 'Please input your quetsion!' }]}>
            <Input />
          </Form.Item>
        </Form>
  </Modal>
  )
}
