import React from 'react';
import { Col, Space, Typography, Badge } from 'antd';

const { Title } = Typography

const colors = [
  'pink',
  'red',
  'yellow',
  'orange',
  'cyan',
  'green',
  'blue',
  'purple',
  'geekblue',
  'magenta',
  'volcano',
  'gold',
  'lime',
];

export default function Category ({ dataSource = [], categoryId, setCategoryId, callback }) {
  const handleClick = (id) => {
    setCategoryId(id)
    callback()
  }
  return (
        <Col span={24}>
            <Space direction="vertical" style={{ width: '100%', }}>
                <Title level={4}>Category</Title>
                {
                    dataSource.map((item, index) =>
                        <Badge
                            key={item.id}
                            color={colors[index]}
                            text={item.categoryName}
                            style={{
                              display: 'inline-block',
                              padding: 2,
                              width: '100%',
                              background: categoryId === item.id ? '#96b2cb' : '#fff',
                              color: categoryId === item.id ? '#fff' : 'rgba(0, 0, 0, 0.85)',
                              cursor: 'pointer'
                            }}
                            onClick={() => handleClick(item.id)}
                        />
                    )
                }
            </Space>
        </Col>
  )
}
