import React, { useState, useEffect } from 'react';
import { Upload, message, Button, Modal } from 'antd';
import ImgCrop from 'antd-img-crop';
import { PlusOutlined, UploadOutlined } from '@ant-design/icons';

const { Dragger } = Upload;

export const getBase64 = (file) =>
  new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = (error) => reject(error);
  });

const isImage = (file) => {
  const ImageReg = /jpeg|png|jpg|gif/;
  return ImageReg.test(file.type);
};

export default function CustomUpload (props) {
  const {
    fileList: defaultFileList = [],
    maxCount = 1,
    listType = 'picture-card',
    accept,
  } = props;

  const [fileList, setFileList] = useState(defaultFileList);

  const [previewProps, setPreviewProps] = useState({
    previewVisible: false,
    previewImage: '',
    previewTitle: '',
  });

  useEffect(() => {
    setFileList(defaultFileList);
  }, [JSON.stringify(defaultFileList)]);

  const handleCancel = () =>
    setPreviewProps({
      ...previewProps,
      previewVisible: false,
    });

  const handleRemove = (file) => {
    const { onChange } = props;

    const files = fileList?.filter((v) => v.uid !== file.uid);
    setFileList(files);

    if (onChange) {
      onChange(files);
    }
  };

  const handleChange = ({ fileList }) => {
    const { onChange } = props;
    const files = fileList.filter((v) => v.status !== 'removed');

    setFileList(files);
    console.log(files)
    if (onChange) {
      onChange(files);
    }
  };

  const handlePreview = async (file) => {
    if (!file.url && !file.preview) {
      Object.assign(file, { preview: (await getBase64(file.originFileObj)) });
    }

    setPreviewProps({
      previewImage: file.url || file.preview || '',
      previewVisible: true,
      previewTitle:
      file.name || (file.url && file.url.substring(file.url.lastIndexOf('/') + 1)) || '',
    });
  };

  const handleBeforeUpload = (file) => {
    if (listType === 'text' && accept) {
      return accept.includes(file.type);
    }
    if (!isImage(file)) {
      message.error('Please correct picture type!');
      return false;
    }
    return true;
  };

  const uploadButton =
        listType === 'text'
          ? (
                <Button icon={<UploadOutlined />}>Upload</Button>
            )
          : (
                <div>
                    <PlusOutlined />
                    <div style={{ marginTop: 8 }}>Click or drag file to this area to upload</div>
                </div>
            );

  return (
        <>
            <ImgCrop
                showGrid
                rotationSlider
                aspectSlider
                showReset
                resetText='Reset'
                modalTitle = 'Edit Avatar'
            >
                <Dragger
                    {...props}
                    multiple={false}
                    beforeUpload={handleBeforeUpload}
                    onPreview={handlePreview}
                    name="file"
                    onChange={handleChange}
                    onRemove={handleRemove}
                    maxCount={1}
                >
                    {fileList && fileList.length >= maxCount ? null : uploadButton}
                </Dragger>
            </ImgCrop>
            <Modal
                open={previewProps.previewVisible}
                title={previewProps.previewTitle}
                footer={null}
                onCancel={handleCancel}
            >
                <img alt="previewImg" style={{ width: '100%' }} src={previewProps.previewImage} />
            </Modal>
        </>
  );
}
