import React, { useEffect } from 'react';
import { Button, Form, Input, Typography, Row, Col, message } from 'antd';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { resetUserPw } from '../services';
import { PasswordStrength } from '../components';

const { Title } = Typography;

export default function Callback () {
  const navigate = useNavigate();
  const [params] = useSearchParams();
  const token = params.get('token')

  useEffect(() => {
    localStorage.setItem('token', token)
  }, [token])

  const onFinish = async (values) => {
    const { password, confirmPassword } = values;
    if (password !== confirmPassword) {
      message.error('Please enter same password');
      return;
    }
    const { message: text } = await resetUserPw({ password });
    if (message) {
      message.success(`Reset Password ${text}!`)
      navigate('/dashboard');
    }
  };

  return (
    <Row align='middle' justify='center' style={{ minHeight: '400px', margin: 24 }}>
      <Col span={24} style={{ textAlign: 'center' }}>
        <Title>Reset Your Password</Title>
      </Col>
      <Col span={24}>
        <Form
          name="basic"
          labelCol={{ span: 8 }}
          wrapperCol={{ span: 8 }}
          onFinish={onFinish}
          autoComplete="off"
        >
          <Form.Item
            label='Password'
            name='password'
            htmlFor='password'
            rules={[
              {
                required: true,
                message: 'Please input your password!',
              },
            ]}>
            <PasswordStrength />
          </Form.Item>
          <Form.Item
            label='Confirm password'
            name='confirmPassword'
            rules={[
              {
                required: true,
                message: 'Please confirm your password!',
              },
            ]}>
            <Input.Password />
          </Form.Item>

          <Form.Item wrapperCol={{ offset: 8, span: 8 }}>
            <Button type="primary" htmlType="submit" block>
              Submit
            </Button>
            Or <a href="/login">login now!</a>
          </Form.Item>
        </Form>
      </Col>
    </Row>
  );
}
