import React, { useState, useEffect } from 'react';
import { Button, Form, Input, Typography, Row, Col, message } from 'antd';
import { resetUserCode } from '../services';

const { Title } = Typography;

export default function Forgot () {
  const [count, setCount] = useState(0);
  const onFinish = async (values) => {
    const { email } = values;
    const { message: text } = await resetUserCode({ email });
    if (text) {
      message.success(`Send Code ${text},Please Check Your Email`)
      setCount(59)
    }
  };

  useEffect(() => {
    const interval = setInterval(() => {
      if (count > 0) {
        setCount(count - 1);
      }
    }, 1000);

    return () => clearInterval(interval);
  }, [count]);

  return (
        <Row align='middle' justify='center' style={{ minHeight: '400px', margin: 24 }}>
            <Col span={24} style={{ textAlign: 'center' }}>
                <Title>Find Your Account</Title>
            </Col>
            <Col span={24}>
                <Form
                    name="basic"
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 8 }}
                    initialValues={{ remember: true }}
                    onFinish={onFinish}
                    autoComplete="off"
                >
                    <Form.Item
                        label="Email"
                        name="email"
                        rules={[{ required: true, message: 'Please input your email!' }]}
                    >
                        <Input />
                    </Form.Item>
                    <Form.Item wrapperCol={{ offset: 8, span: 8 }}>
                        <Button type="primary" htmlType="submit" block disabled={count > 0}>
                            {count > 0 ? `${count}s left to Resend Code` : 'Send Code'}
                        </Button>
                        Or <a href="/login">login now!</a>
                    </Form.Item>
                </Form>
            </Col>
        </Row>
  );
}
