import React from 'react';
import { Comment, List, Tooltip } from 'antd';

export default function Comments ({ dataSource = [] }) {
  const length = dataSource ? dataSource.length : 0
  return (
        <List
            dataSource={dataSource}
            header={`${length} ${length > 1 ? 'replies' : 'reply'}`}
            itemLayout="horizontal"
            renderItem={props =>
                <Comment
                    key={props.id}
                    author={<a>{props.user ? props.user.username : '-'}</a>}
                    content={
                        <p>
                            {props.content}
                        </p>
                    }
                    datetime={
                        <Tooltip title={props.updatedAt ? props.updatedAt.split('T')[0] : '-'}>
                            <span>{props.updatedAt ? props.updatedAt.split('T')[0] : '-'}</span>
                        </Tooltip>
                    }
                />
            }
        />
  );
}
