import React from 'react';
import { Modal, Form, Input, message } from 'antd';

export default function AnswerModal ({ visible, setVisible, callback, service, questionId }) {
  const [form] = Form.useForm();
  const handleOk = async () => {
    form.validateFields().then(async values => {
      const { message: text } = await service({
        ...values,
        questionId
      });
      if (text) {
        message.success(`Add New Answer ${text}!`)
        callback();
        form.resetFields();
        setVisible(!visible)
      }
    })
  }
  return (
    <Modal
    key='Add New Answer'
    title='Add New Answer'
    open={visible}
    onCancel={() => setVisible(!visible)}
    onOk={handleOk}
  >
        <Form
          form={form}
          autoComplete="off"
        >
          <Form.Item
            label="answer"
            name="content"
            rules={[{ required: true, message: 'Please input your answer!' }]}>
            <Input />
          </Form.Item>
        </Form>
  </Modal>
  )
}
