import { request } from '../utils';

export async function questionInsert (data) {
  return request('/api/question', {
    method: 'POST',
    data,
  });
}

export async function questionSingle ({ questionId }) {
  return request(`/api/question/${questionId}`, {
    method: 'GET',
  });
}

export async function questionCourse ({ courseId }) {
  return request(`/api/question/course/${courseId}`, {
    method: 'GET',
  });
}

export async function questionCourseCategory ({ courseId, categoryId }) {
  return request(`/api/question/course/${courseId}/category/${categoryId}`, {
    method: 'GET',
  });
}

export async function questionSearch (data) {
  return request('/api/question/search', {
    method: 'POST',
    data
  });
}
