import React from 'react';
import { Col, Avatar, Space, List, Tag, Typography } from 'antd';

const { Title, Text } = Typography

export default function Question ({ dataSource = [], questionId, setQuestionId, onClick }) {
  const handleClick = (id) => {
    setQuestionId(id);
    onClick(id)
  }

  return (
        <Col span={24}>
            <List
                itemLayout='vertical'
                dataSource={dataSource}
                renderItem={item => (
                    <List.Item
                        key={item.id}
                        style={{
                          cursor: 'pointer',
                          background: item.id === questionId ? '#96b2cb' : '#fff'
                        }}
                        onClick={() => handleClick(item.id)}
                    >
                        <List.Item.Meta
                            avatar={
                                <Avatar style={{ verticalAlign: 'middle' }} size="large">
                                    {item.user ? item.user.username : '-'}
                                </Avatar>
                            }
                            title={<Title level={3}>{item.content}</Title>}
                            description={<Text>{item.createdAt ? item.createdAt.split('T')[0] : '-'}</Text>}
                        />
                        <Space>
                            <Tag color="orange">{item.course ? item.course.courseName : '-'}</Tag>
                            <Tag color="magenta">{item.category ? item.category.categoryName : '-'}</Tag>
                        </Space>
                    </List.Item>
                )}
            />
        </Col>
  )
}
