import React, { useState } from 'react';
import { Layout, Button, Col, Row, Avatar, message } from 'antd';
import { useNavigate } from 'react-router-dom';
import { courseInsert, currentUserUpdate, logout } from '../services';
import { CourseModal, UserInfoModal } from '../containers';

const { Header } = Layout;

const css = {
  mainColor: '#51288c'
}

export default function CustomHeader ({ getCourses, getCurrentUser, userInfo, visible, setVisible }) {
  const navigate = useNavigate();
  const [userNameVisible, setUserNameVisible] = useState(false);

  const handleUserItemClick = async (key) => {
    if (key === 'logout') {
      await logout();
      message.success('logout success!')
      navigate('/login')
    }
    if (key === 'update') {
      setUserNameVisible(!userNameVisible);
    }
    if (key === 'login') {
      navigate(`/${key}`)
    }
  }

  const buttons = userInfo.id
    ? [
        {
          key: 'update',
          label: 'Edit UserName',
        },
        {
          key: 'logout',
          label: 'logout',
        },
      ]
    : [
        {
          key: 'login',
          label: 'login',
        },
      ]

  return (
    <Header style={{ padding: 0, background: 'primary', backgroundColor: css.mainColor, overflow: 'hidden', height: 64 }}>
      <CourseModal
        visible={visible}
        setVisible={setVisible}
        service={courseInsert}
        callback={getCourses}
      />
      <UserInfoModal
        userInfo={userInfo}
        visible={userNameVisible}
        setVisible={setUserNameVisible}
        service={currentUserUpdate}
        callback={getCurrentUser}
      />
      <Row align='middle' justify='space-between' style={{ height: 64 }}>
        <Col className="logo" style={{ height: 64, paddingLeft: 8, color: '#fff', fontWeight: 700 }}>
          Online Discussion Forum
        </Col>
        <Col style={{ paddingRight: 16 }}>
          {
            buttons.map(button =>
              (<Button
              type='link'
              key={button.key}
              onClick={() => handleUserItemClick(button.key)}
              style={{ color: '#fff' }}
            >
              {button.label}
            </Button>
              )
            )
          }
          <Avatar src={userInfo.avatar} style={{ verticalAlign: 'middle', cursor: 'pointer', color: css.mainColor, backgroundColor: '#fff' }} size="small" gap={1}>
            {userInfo.username}
          </Avatar>
        </Col>
      </Row>
    </Header>
  )
}
