import React, { useEffect } from 'react';
import { Modal, Form, Input, message } from 'antd';
import { Upload } from '../components';
import { getBase64 } from '../components/Upload';

export default function UserInfoModal ({ visible, setVisible, callback, service, userInfo }) {
  const [form] = Form.useForm();

  useEffect(() => {
    form.setFieldsValue({
      username: userInfo.username,
      avatar: userInfo.avatar
        ? [
            {
              uid: '-1',
              name: 'avatar',
              url: userInfo.avatar,
            },
          ]
        : undefined,
    })
  }, [userInfo, form])

  const handleOk = async () => {
    form.validateFields().then(async values => {
      const result = {}
      const { avatar, username } = values
      if (avatar instanceof Array && avatar.length > 0) {
        result.avatar = /base64/.test(avatar[0].url) ? avatar[0].url : await getBase64(avatar[0].originFileObj)
      }
      result.username = username;
      const { message: text } = await service(result);
      if (text) {
        message.success(`Edit UserInfo ${text}!`)
        callback();
        setVisible(!visible)
      }
    })
  }

  return (
    <Modal
      key='Edit UserInfo'
      title='Edit UserInfo'
      open={visible}
      onCancel={() => setVisible(!visible)}
      onOk={handleOk}
    >
      <Form
        form={form}
        autoComplete="off"
      >
        <Form.Item
          label="UserName"
          name="username"
          rules={[{ required: true, message: 'Please input your userName!' }]}>
          <Input />
        </Form.Item>
        <Form.Item
          label="avatar"
          name="avatar"
          valuePropName='fileList'
          rules={[{ required: true, message: 'Please upload your avatar!' }]}>
          <Upload
            maxCount={1}
            listType="picture-card"
          />
        </Form.Item>
      </Form>
    </Modal>
  )
}
