import React from 'react';
import { Modal, Form, Input, message } from 'antd';

export default function CourseModal ({ visible, setVisible, callback, service }) {
  const [form] = Form.useForm();
  const handleOk = async () => {
    form.validateFields().then(async value => {
      const { message: text } = await service(value);
      if (text) {
        message.success(`Add New Course ${text}!`)
        callback();
        setVisible(!visible)
      }
    })
  }
  return (
    <Modal
    key='Add New Course'
    title='Add New Course'
    open={visible}
    onCancel={() => setVisible(!visible)}
    onOk={handleOk}
  >
        <Form
          form={form}
          autoComplete="off"
        >
          <Form.Item
            label="CourseName"
            name="courseName"
            rules={[{ required: true, message: 'Please input your courseName!' }]}>
            <Input />
          </Form.Item>
        </Form>
  </Modal>
  )
}
