import CryptoJS from 'crypto-js';
export * from './request';

const secretKey = 'SecretKey';

export const setCookie = (key, value, expireDay) => {
  const d = new Date();
  d.setTime(d.getTime() + (expireDay * 24 * 60 * 60 * 1000));
  const expires = 'expires=' + d.toUTCString();
  document.cookie = key + '=' + CryptoJS.AES.encrypt(encodeURI(value), secretKey).toString() + '; ' + expires;
}

export const clearCookie = (key) => {
  setCookie(key, '', -1);
}

export const getCookie = (key) => {
  const name = key + '=';
  const str = document.cookie.split(';');
  for (let i = 0; i < str.length; i++) {
    let val = str[i];
    while (val.charAt(0) === ' ') val = val.substring(1);
    if (val.indexOf(name) !== -1) {
      const value = decodeURI(val.substring(name.length, val.length));
      return CryptoJS.AES.decrypt(value, secretKey).toString(CryptoJS.enc.Utf8);
    }
  }
  return '';
}
